.animation-container {
    position: relative;
    width: 352;

    .the-cloak {
        filter: invert(100%) sepia(95%) saturate(0%) hue-rotate(73deg) brightness(103%) contrast(106%);
        opacity: 0;
        width: 344px;
    }

    .the-cloak-animate {
        animation: loading 200ms ease-in-out 400ms 1 normal forwards;
        filter: invert(100%) sepia(95%) saturate(0%) hue-rotate(73deg) brightness(103%) contrast(106%);
        opacity: 0;
        width: 344px;
    }

    .elder-wand {
        background-color: white;
        height: 240px;
        left: 174px;
        opacity: 0;
        position: absolute;
        top: 54px;
        width: 4px;
    }

    .elder-wand-animate {
        animation: loading 600ms ease-in-out 0s 1 normal forwards;
        background-color: white;
        height: 240px;
        left: 174px;
        opacity: 0;
        position: absolute;
        top: 54px;
        width: 4px;
    }

    .resurrection-stone {
        border: 4px solid white;
        border-radius: 50%;
        height: 140px;
        left: 102px;
        opacity: 0;
        position: absolute;
        top: 144px;
        width: 140px;
    }

    .resurrection-stone-animate {
        animation: loading 400ms ease-in-out 200ms 1 normal forwards;
        border: 4px solid white;
        border-radius: 50%;
        height: 140px;
        left: 102px;
        opacity: 0;
        position: absolute;
        top: 144px;
        width: 140px;
    }

    @keyframes loading {
        from {
            display: 0;
        }

        to {
            opacity: 100;
        }
    }
}
