html,
body {
    background-color: black;
}

.main-page {
    color: white;
    left: 50%;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 352px;

    h1 {
        
        margin: 24px 0px;
        
    }

    .engagement-photo {
        clip-path: ellipse(120px 160px);
        height: 320px;
        object-fit: cover;
        width: 360px;
    }

    .input-block { 
        margin: 24px auto;
        width: 240px;

        label {
            display: block;
        }

        .label-error {
            display: block;
            color: red;
            margin: 8px 0px;
        }

        input {
            box-sizing: border-box;
            width: 100%;
        }

        .input-error {
            border: 2px solid red;
            box-sizing: border-box;
            width: 100%;
        }

        button {
            background-color: transparent;
            border-radius: 4px;
            color: white;
            border: solid white 2px;
            margin-top: 24px;
            width: 100%;
        }

        button:hover {
            background-color: white;
            border-radius: 4px;
            color: black;
            border: solid white 2px;
            margin-top: 24px;
            width: 100%
        }
    }

    .confirmed {
        p {
            font-size: 24px;
        }

        .rsvp-form {
            width: 352px;

            .input-container {
                display: flex;
                flex-direction: column;
                margin: 0 auto;
                width: 240px;

                label {
                    border: solid white 1px;
                    border-radius: 4px;
                    margin-bottom: 16px;
                    padding: 16px 0px;
                }

                label:hover,
                .active-label {
                    background-color: white;
                    border: solid white 1px;
                    border-radius: 4px;
                    color: black;
                    margin-bottom: 16px;
                    padding: 16px 0px;
                }

                button {
                    background-color: transparent;
                    border: solid 1px white;
                    border-radius: 4px;
                    color: white; 
                    margin-top: 24px;
                    padding: 8px 0px;
                }

                button:hover {
                    background-color: white;
                    border: solid 1px white;
                    border-radius: 4px;
                    color: black; 
                    margin-top: 24px;
                    padding: 8px 0px;
                }
            }
    
            input {
                height: 0px;
                opacity: 0;
                position: absolute;
                width: 0px;
            }
        }
    }

    .yes-response {
        h3 {
            margin-bottom: 8px;
        }

        .line {
            background-color: white;
            height: 2px;
            margin: 0px auto;
            width: 240px;
        }

        .details-container {
            margin: 0 auto;
            width: 240px;
            text-align: left;

            table {
                tbody {
                    th {
                        vertical-align: top;
                    }
                }
            }
        }
    }
}
